<template>
  <div class="grid">
    <br><br>
    <br><br>
    <vs-row align="center" justify="center">
      <vs-col w="6" style="text-align:center">
        <div class="center con-avatars">
          <vs-avatar size="90" style="margin:auto;font-size: 2rem;"  :loading="isLoaded">
            
            <img :src="user.thumbnailPhoto"  v-if="profile">
            <template #text v-if="!profile">
                {{user.name}}
            </template>
          </vs-avatar>
          
          <h1>
            Vtejte, {{user.name}}
          </h1>
          <p>Zde spravujte sv daje pro pihlaovn k webm, systmu Fujin a operanm systmm Windows (vyaduje Fujin Credential Provider Service for Windows) a macOS (vyaduje nastaven sovch t). </p>
        </div>

      </vs-col>
    </vs-row> 
    <br>
    <vs-row align="center" justify="center">
      <vs-col w="4" vs-type="flex" vs-justify="center" vs-align="center" style="justify-content: center;display: flex;">
        <vs-card type="3" @click="$router.push({ path: '/security' })">
    <template #title>
      <h3>Zabezpete si et</h3>
    </template>
    <template #img>
      <img src="https://cdn.vespotok.net/img/domains-security.png" alt="">
    </template>
    <template #text>
      <p>
        Internet je pln rizik, chrate se ped napadenm aktivn zmnou hesla a pouvnm dvoufaktorovho ovovn.
      </p>
    </template>
    <template #interactions>
      <vs-button danger icon>
        <i class='bx bx-lock-open-alt'></i>
        <span class="span">
          Zabezpeen
        </span>
      </vs-button>
      
    </template>
  </vs-card>
      </vs-col>
      <vs-col w="4" vs-type="flex" vs-justify="center" vs-align="center" style="justify-content: center;display: flex;">
        <vs-card type="3" @click="$router.push({ path: '/federations' })">
    <template #title>
      <h3>Zkontrolujte federace</h3>
    </template>
    <template #img>
      <img src="https://cdn.vespotok.net/img/domains-federations.png" alt="">
    </template>
    <template #text>
      <p>
        Federace tu znamen propojen, propojte svj et s Facebookem, LinkedInem, Google tem, i spravujte sv propojen s domnovmi slubami Active Directory.
      </p>
    </template>
    <template #interactions>
      <vs-button danger icon>
        <i class='bx bx-shape-triangle'></i>
        <span class="span">
          Federace
        </span>
      </vs-button>
      
    </template>
  </vs-card>
      </vs-col>
      
    </vs-row>
  </div>
</template>
<script>
  export default {
    name: 'MainPage',
    components: {

    },
    data() {
      return {
        user: this.$store.state,
        isLoaded: true,
        profile: true
      }
    },
    created() {
      this.$parent.searchingDomain = false;
      this.$parent.activePage = "/";
      this.isLoaded = false;
      if(this.user.thumbnailPhoto == undefined || this.user.thumbnailPhoto == "")
      {
        this.profile = false;
      }
    },
  }
</script>